.gpt3__features-container__feature{
    width:100%;
    display:flex;
    justify-content: space-between;
    align-items: flex-start;
    margin:1rem;
}

.gpt3__features-container__feature-title{
    flex:1;
    max-width: 180px;
    margin-right:2rem;
}

.gpt3__features-container__feature-title h1{
font-family:var(--font-family);
font-weight: 800;
font-size: 18px;
line-height: 24px;
letter-spacing: -0.04em;
color: #FFFFFF;
}

.gpt3__features-container__feature-title div{
    width: 38px;
    height: 3px;
    margin-bottom: 0.3rem;
    background: linear-gradient(103.22deg, #AE67FA -13.86%, #F49867 99.55%), #FFFFFF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.gpt3__features-container_feature-text{
    flex:2;
    max-width:650px;
    display: flex;
    font-family:var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: var(--color-text);
}

@media screen and (max-width:550px){
    .gpt3__features-container__feaure-title h1{
        font-size:14px;
        line-height:22px
    }
    .gpt3__features-container__feaure-title h1{
        font-size: 12px;
        line-height: 20px;
    }
    .gpt3__features-container__feaure{
        margin: 1rem 0;
    }
}