.gpt3__navbar{
    display: flex;
    justify-content:space-between;
    align-items:center;
    padding:2rem 6rem;
}

.gpt3__navbar-links{
    flex:1;
    display:flex;
    justify-content: flex-start;
    align-items: center;
}

.gpt3__navbar-links_logo{
    margin-right:2rem;
}
.gpt3__navbar-links_logo img{
    width:62.56px; 
    height:16.02px;
}
.gpt3__navbar-links_container{
    display:flex;
}

.gpt3__navbar-sign{
    display:flex;
    justify-content: flex-end;
    align-items: center;
}

.gpt3__navbar-links_container p,
.gpt3__navbar-sign p,
.gpt3__navbar-menu_container p{
    color:#fff;
    font-family: var(--font-family);
    font-weight:500;
    font-size:18px;
    line-height: 25px;
    text-transform: capitalize;
    margin:0 1rem;
    cursor:pointer;
}

.gpt3__navbar-sign button,
.gpt3__navbar-menu_container button{
padding:.5rem 1rem;
color:#fff;
background: #FF4820;
border-radius: 5px;
font-weight: 500;
font-size: 18px;
line-height: 25px;
font-family:var(--font-family) ;
border:none; outline:none;
cursor: pointer;
}

.gpt3__navbar-menu{
    margin-left:1rem;
    display:none;
    position:relative;
}
.gpt3__navbar-menu svg{
    cursor:pointer;
}
.gpt3__navbar-menu_container{
    display: flex;
    justify-content: flex-end;
    align-items:flex-end;
    flex-direction:column;
    text-align: end;
    background: var(--color-footer);
    padding:2rem;
    margin-top: 1rem;
    position: absolute;
    top:40px;
    right:0;
    border-radius:5px;
    min-width:210px;
    box-shadow:0 0 5 rgba(0,0,0,0.2);
}

.gpt3__navbar-menu_container p{
    margin:1rem 0 ;
}
.gpt3__navbar-menu_container-links-sign{
    display:none
}

@media screen and (max-width:1050px){
    .gpt3__navbar-links_container{
        display:none;
    }
    .gpt3__navbar-menu{
        display: flex;
    }
}

@media screen and (max-width:700px){
    .gpt3__navbar{
        padding:2rem 4rem;
    }
}
@media screen and (max-width:550px){
    .gpt3__navbar{
        padding:2rem ;
    }
    .gpt3__navbar-links_container{
        display:none;
    }
    .gpt3__navbar-sign{
        display:none;
    }
    .gpt3__navbar-menu_container{
        top:20px;
    }
    .gpt3__navbar-menu_container-links-sign{
        /* display: flex;
        justify-content: center;
        align-items: flex-end;
        flex-direction: column; */
        display:block;
         
    }
}